#content-tool {
	background: #FFFFFF;
	height: 100%;
	padding-left: 20px;
	padding-right: 20px;
	padding-top: 14px;
	.top {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 8px;
	}
	.left-part {
		display: flex;
		align-items: center;
	}
	.right-part {
		display: flex;
		.btn-default {
			padding-left: 14px;
			padding-right: 15px;
			font-weight: 600;
			margin-right: 7px;
			.fa-table {
				margin-right: 6px;
				color: #3AA9DE;
				font-size: 15px;
				position: relative;
    			top: 1px;
			}
		}
	}
	.download {
		padding: 0 5px;
		color: #3AA9DE;
		transition: all 0.1s ease-out;
		border-radius: 2px;
		font-size: 17px;
		&:hover {
			background: #DADADA;
		}
	}
	.print {
		padding: 0 5px;
		color: #3AA9DE;
		transition: all 0.1s ease-out;
		border-radius: 2px;
		font-size: 17px;
		&:hover {
			background: #DADADA;
		}
	}
	.react-datepicker-wrapper {
		width: 62px;
		height: 100%;
		display: flex;
		.react-datepicker__input-container {
			width: 100%;
			height: 100%;
		}
		input {
			width: 100%;
			display: block;
			border: 1px solid #C9C9C9;
			border-top-left-radius: 2px;
			border-bottom-left-radius: 2px;
			height: 100%;
			font-size: 10px;
			color: #0c0c0c;
			padding-left: 3px;
		}
	}
	.date-picker-wrap {
		height: 18px;
		display: flex;
		margin-right: 17px;
		.calendar {
			background: #3AAADE;
			color: #FFFFFF;
			width: 20px;
			height: 100%;
			border-top-right-radius: 2px;
			border-bottom-right-radius: 2px;
		}
		.fa-calendar {
			position: relative;
    		top: -1px;
    		left: -1px;
    		font-size: 11px;
		}
	}
	.updated {
		font-size: 12px;
		color: #4b4b4b;
		font-weight: 300;
		margin-right: 12px;
		b {
			font-weight: 600;
			margin-left: 2px;
		}
	}
	.react-datepicker__day {
		width: 1.2rem;
    	line-height: 1.2rem;
    	font-size: 11px;
    	&:hover {
    		border-radius: 2px;
    	}
	}
	.react-datepicker__current-month {
		font-size: 12px;
    	font-weight: 600;
	}
	.react-datepicker__day-name {
		font-size: 12px;
		width: 1.2rem;
    	line-height: 1.2rem;
	}
	.react-datepicker__day--keyboard-selected {
		border-radius: 2px;
	}
	.react-datepicker__day--outside-month {
		opacity: 0.5;
	}
	.react-datepicker__header {
		background-color: #DEDEDE;
	}
	.react-datepicker-popper[data-placement^="bottom"] .react-datepicker__triangle {
		border-bottom-color: #DEDEDE;
	}
	.multi-select {
		input {
			height: 25px !important;
    		line-height: 24px;
    		border: 1px solid #CFCFCF;
    		padding: 5px;
    		border: 1px solid rgb(222, 226, 228);
		}
	}
	.dropdown-heading-value {
		min-width: 70px;
		font-size: 12px;
		line-height: 20px !important;
		max-width: none;
		width: auto;
		position: relative !important;
		padding-left: 5px !important;
    	padding-right: 0px !important;
	}
	.dropdown-heading {
		height: 20px !important;
		width: auto;
		display: flex !important;
		align-items: center !important;
		border-radius: 2px !important;
		border: 1px solid #C8C7C8 !important;
	}
	.select-item {
		padding: 0 4px !important;
		font-size: 12px;
	}
	.item-renderer {
		display: flex;
		align-items: center;
		width: 100%;
	}
	.dropdown-heading-dropdown-arrow {
		padding-right: 0 !important;
		background: #43AADB !important;
		width: 20px !important;
		span {
			border-width: 5px 5px 2.5px !important;
			border-color: rgb(255, 255, 255) transparent transparent !important;
		}
	}
}
#table-settings-dialog {
	.title {
		color: #4b4b4b;
    	font-size: 16px;
    	font-family: 'Open Sans', sans-serif;
	}
	.title-wrap {
		padding: 0;		
		border-bottom: 1px solid #DFDFDF;
		padding-left: 17px;
		h2 {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding-top: 2px;
		}
	}
	.MuiDialog-paperWidthSm-12 {
		width: 480px;
	}
	.MuiIconButton-root-93 {
		padding: 8px;
	}
	.MuiDialogContent-root-108 {
		padding: 15px 17px 17px;
	}
	.dialog-content {

	}
	.cols {
		display: flex;
	}
	.col {
		padding-right: 26px;
		&:last-child {
			margin-left: auto;
			margin-right: 0;
		}
	}
	.text {
		font-size: 14px;
		color: #4b4b4b;
		font-weight: 300;
		margin-bottom: 13px;
	}
	.col-titile {
		color: #4b4b4b;
		font-size: 16px;
		margin-bottom: 4px;
	}
}