@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,400i,600,700&subset=cyrillic');
* {
	box-sizing: border-box;
}
html {
	font-family: 'Open Sans', sans-serif;
}
body {
  margin: 0;
  padding: 0;
  background: #F3F3F4;
}
.content {
	padding: 35px 30px 25px 30px;
	height: calc( 100vh - 67px);
}
.btn.primary-btn {
	color: #FFFFFF;
	background: #3AAADE;
	font-size: 14px;
	font-family: 'Open Sans', sans-serif;
	height: 30px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 2px;
	padding-left: 20px;
	padding-right: 20px;
	box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.1), 
	    0px 2px 2px 0px rgba(0,0,0,0.07), 
	    0px 3px 1px -2px rgba(0,0,0,0.06);
	transition: all 0.1s ease-out;
	position: relative;
	&:hover {
		background: #2F87D9;
	}
}
.btn.btn-default {
	color: #4b4b4b;
	background: transparent;
	font-size: 14px;
	font-family: 'Open Sans', sans-serif;
	height: 30px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 2px;
	padding-left: 20px;
	padding-right: 20px;
	transition: all 0.1s ease-out;
	&:hover {
		background: #DADADA;
	}
}

.btn.outlined-primary-btn {
	font-size: 14px;
	color: #2daae1;
	border-radius: 2px;
	border: 2px solid #3AAADE;
	font-family: 'Open Sans', sans-serif;
	height: 30px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding-left: 20px;
	padding-right: 20px;
	transition: all 0.1s ease-out;
	&:hover {
		/* color: #FFFFFF;
		background: #3AAADE; */
	}
}
.btn.fetching {
	background: rgba(0, 0, 0, 0.12);
	color: rgba(0, 0, 0, 0.26);
	&:hover {
		background: rgba(0, 0, 0, 0.12);
		color: rgba(0, 0, 0, 0.26);
	}
}
.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in;
}

.link {
	text-decoration: none;
}